@for $i from 4 through 31 {

    .min-height-#{$i} {
      ::ng-deep {
        .ck-editor__editable_inline {
          min-height: $i * 1rem;
        }
      }
    }
    .max-height-#{$i} {
      ::ng-deep {
        .ck-editor__editable_inline {
          max-height: $i * 1rem;
        }
      }
    }
  }