.usa-tag:last-of-type {
  margin-right: units(0.5);
}

// Info tags
.sds-tag--info-purple {
  @include u-bg("violet-warm-10v");
  @include u-border("indigo-warm-50");
  @include u-border("1px");
  font-weight: font-weight("semibold");
  border-radius: 0;
  @include u-text("base-darker");
}

.sds-tag--info-white {
  background-color: white;
  border: 1px solid #76766a;
  font-weight: font-weight("semibold");
  border-radius: 0;
  @include u-text("base-darker");
}

// Chip tag

.sds-tag.sds-tag--chip {
  background-color: color("primary-lighter");
  border: 1px solid color("primary");
  @include u-font("sans", "3xs");
  padding: 2px 5px;
  @include u-display("inline-flex");
  @include u-align-items("align-start");
  @include u-margin-top("05");
}

.sds-tag.sds-tag--chip .sds-tag__close {
  @include button-unstyled;
  font-size: 12px;
  margin-left: 5px;
  @include u-text("ink");
  cursor: pointer;

  &:hover {
    @include u-text("base");
  }
}

// Chip tag input
.sds-tag.sds-tag--input {
  max-width: units($theme-input-max-width);
  @include u-width("full");
  @include u-position("relative");

  .sds--tag__item {
    div:first-child,
    .sds-tag__primary-text {
      @include u-text("semibold");
    }
  }

  .sds-tag__close {
    @include u-position("absolute");
    @include u-top("05");
    @include u-right("05");
  }
}

// Status tag

.sds-tag[class*="sds-tag--status"] {
  white-space: nowrap;
  @include u-font("sans", "3xs");
  @include u-text("base-dark");
  line-height: 20px;
  font-weight: font-weight("semibold");
  padding: 0 5px 4px 0;
  border-radius: 0;
  margin-right: 5px;
  width: auto;
  display: flex;
  align-items: center;
}

// Outline tag

.sds-tag[class*="sds-tag--outline"] {
  @include u-font("sans", "3xs");
  @include u-text("base-dark");
  @include u-border("1px");
  line-height: 1rem;
  font-weight: font-weight("semibold");
  padding: 6px 7px 7px 7px;
  @include u-border("base-light");
  margin-right: 14px;
  display: flex;
  width: 128px;
  @include u-bg("base-lightest");
}

.sds-tag--outline {
  &--blue {
    @include u-border("info-dark");
  }
  &--red {
    @include u-border("error");
  }
  &--green {
    @include u-border("success");
  }
  &--gray {
    @include u-border("base-light");
  }
  &--dark-gray {
    @include u-border("base");
  }
  &--yellow {
    @include u-border("warning");
  }
  &--blue {
    @include u-border("secondary-vivid");
  }
  &--light-blue {
    @include u-border("info");
  }
  &--brown {
    @include u-border("warning-darker");
  }

  &--danger {
    @include u-border("error");
  }
  &--info {
    @include u-border("info");
  }
  &--info-lighter {
    @include u-border("info-lighter");
  }
  &--info-light {
    @include u-border("info-light");
  }
  &--info-dark {
    @include u-border("info-dark");
  }
  &--info-darker {
    @include u-border("info-darker");
  }
  &--error {
    @include u-border("error");
  }
  &--error-lighter {
    @include u-border("error-lighter");
  }
  &--error-light {
    @include u-border("error-light");
  }
  &--error-dark {
    @include u-border("error-dark");
  }
  &--error-darker {
    @include u-border("error-darker");
  }
  &--warning {
    @include u-border("warning");
  }
  &--warning-lighter {
    @include u-border("warning-lighter");
  }
  &--warning-light {
    @include u-border("warning-light");
  }
  &--warning-dark {
    @include u-border("warning-dark");
  }
  &--warning-darker {
    @include u-border("warning-darker");
  }
  &--success {
    @include u-border("success");
  }
  &--success-lighter {
    @include u-border("success-lighter");
  }
  &--success-light {
    @include u-border("success-light");
  }
  &--success-dark {
    @include u-border("success-dark");
  }
  &--success-darker {
    @include u-border("success-darker");
  }
  &--disabled {
    @include u-border("disabled");
  }
}

.sds-tag-container {
  @extend .flex-align-stretch;
}

.sds-tag-container > .sds-tag:nth-child(n + 2) {
  @include u-border-top(0);
}

.sds-tag--small {
  @include u-font("sans", "3xs");
}

.sds-tag--disabled {
  @include u-bg("disabled", !important);
  @include u-border("disabled-dark", !important);
}

// Block style tag
div[class^="grid-col-"] .sds-tag--block,
div[class*=" grid-col-"] .sds-tag--block {
  display: block !important;
  width: 100% !important;
}

/*
----------------------------------------
Label Title
----------------------------------------
*/
.sds-type--label-title {
  font-size: 14px;
  font-style: italic;
  font-weight: font-weight("thin");
  margin: 0;
  color: #5d5d52;
}

.sds-tag--light {
  @include u-bg("white", !important);
  @include u-border("warning", !important);
  @include u-text("base-darker", !important);
}

.sds-static--tag {
  @include u-display("block");
  @include u-margin-bottom(0);
  @include u-margin-top("2px");
  @include u-margin-right(0.5, !important);
  @include u-font("sans", "3xs");
  min-width: 52px;
  width: 52px;
}
